import { Input, Form, Panel } from "rsuite";
import { BasicView } from "../Main/basicView";

const KEY_NAMES = {
    "transaction_id": "ID",
    "ip": "IP",
    "device_id": "ID ",
    "device_type": " ",
    "tran_code": " ",
    "mcc": "",
    "client_id": "ID ",
    "card_type": " ",
    "pin_inc_count": "  ",
    "card_status": " ",
    "expiration_date": " ",
    "datetime": "  ",
    "sum": "",
    "oper_type": " ",
    "balance": "",
    "is_fraud": ""
}

export function SingleLogView () {
    return (
        <BasicView>
            <Panel header={" "} bordered className="log-form">
                <Form fluid>
                    {
                        Object.keys(KEY_NAMES).map((key) => (
                            <Form.Group controlId="log">
                                <Form.ControlLabel>{KEY_NAMES[key]}</Form.ControlLabel>
                                <Form.Control name={key} type="text" />
                            </Form.Group>
                        ))
                    }
                </Form>
            </Panel>
        </BasicView>
    )
}