import React, { useEffect } from "react";
import { Link } from "react-router-dom";
import "./Header.css";
import benzovozlogo from '../../images/benzovoz.png'

import { Drawer, useToaster, Form, Panel, Button, Message } from "rsuite";

const KEY_NAMES = {
  "transaction_id": "ID ",
  "ip": "IP ",
  "device_id": "ID ",
  "device_type": " ",
  "tran_code": " ",
  "mcc": "",
  "client_id": "ID ",
  "card_type": " ",
  "pin_inc_count": "  ",
  "card_status": " ",
  "expiration_date": "  ",
  "datetime": "   ",
  "sum": " ",
  "oper_type": " ",
  "balance": " ",
}

const checkLog = (logForm, toaster) => {
  fetch(`${process.env.REACT_APP_API_URL}/predict/`, {
    body: JSON.stringify(logForm),
    method: "POST",
    headers: {
      "Content-Type": "application/json",
    },
  }).then((response) => response.json()).then((data) => {
    console.log(data)
    toaster.push(
      <Message type="success" header=" " key="success" closable>
        {` ${data ? "" : " "}        .`}
      </Message>,
      {
        placement: "bottomStart",
        duration: 5000
      }
    )
  })
}

const Header = ({ isAuthenticated, onLogout, toaster}) => {
  const [drawerOpen, setDrawerOpen] = React.useState(false);
  const [logForm, setLogForm] = React.useState({
    "transaction_id": "string",
    "ip": "string",
    "device_id": "string",
    "device_type": "string",
    "tran_code": "string",
    "mcc": "string",
    "client_id": "string",
    "card_type": "string",
    "pin_inc_count": 0,
    "card_status": "string",
    "expiration_date": "string",
    "datetime": "string",
    "sum": 0,
    "oper_type": "string",
    "balance": 0
  });

  return (
    <nav className="navbar navbar-expand-lg navbar-dark bg-success">
      <div className="container-fluid">
        <img
        src = {benzovozlogo}
        alt = ""
        className="benzovoz_logo"
        ></img>
        <div className="collapse navbar-collapse">
          {isAuthenticated && (
            <ul className="navbar-nav me-auto">
              <li className="nav-item">
                <Link className="btn btn-outline-light me-2" to="/stats"> </Link>
              </li>
              <li className="nav-item">
                <Link className="btn btn-outline-light me-2" to="/logs"></Link>
              </li>
              <li className="nav-item">
                <Link className="btn btn-outline-light" onClick={() => setDrawerOpen(true)}>  </Link>
              </li>
            </ul>
          )}
          <ul className="navbar-nav ms-auto">
            {isAuthenticated ? (
              <li className="nav-item">
                <button className="btn btn-outline-light" onClick={onLogout}>
                  
                </button>
              </li>
            ) : (
              <>
                <li className="nav-item btn-outline-light login">
                  <Link className="btn btn-outline-light" to="/login"></Link>
                </li>
                <li className="nav-item">
                  <Link className="btn btn-outline-light login" to="/register"></Link>
                </li>
              </>
            )}
          </ul>
        </div>
      </div>
      
      <Drawer
        placement="right"
        size="sm"
        open={drawerOpen}
        onClose={() => setDrawerOpen(false)}
      >
        <Drawer.Header>
          <Drawer.Title> </Drawer.Title>
        </Drawer.Header>
        <Drawer.Body>
          <Panel className="">
                <Form fluid>
                    {
                        Object.keys(KEY_NAMES).map((key) => (
                            <Form.Group controlId="log">
                                <Form.ControlLabel>{KEY_NAMES[key]}</Form.ControlLabel>
                                <Form.Control onChange={(e) => setLogForm((prev) => ({ ...prev, [key]: e }))} name={key} type="text" />
                            </Form.Group>
                        ))
                    }
                </Form>
                <Button style={{ marginTop: "1rem" }} appearance="primary" color="green" block onClick={
                  () => checkLog(logForm, toaster)
                }></Button>
            </Panel>
        </Drawer.Body>
      </Drawer>
    </nav>
  );
};

export default Header;
